  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
  text-align: center;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

:root {
  --primary-text-color: white;
  --primary-text-size: 1rem;
  --primary-text-shadow: 2px 2px 8px white;
  --primary-font-family: Arial, Helvetica, sans-serif;
  --secondary-text-color: black;
}

/* Body Background */
body {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  font-family: var(--primary-font-family);
}

/* Header */
.header {
  display: flex;
  justify-content: center;
  gap: 0.8rem;
  font-size: 1.8rem;
  margin-top: 3rem;
  color: var(--primary-text-color);
  font-weight: bold;
  text-shadow: var(--primary-text-shadow);
}

.header > span {
  color: black;
  text-shadow: var(--primary-text-shadow);
}

/* Main Container (Pop-up) */
.popup-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.8);
}

/* Flex Container for Weather Overview and Forecast */
.main-container {
  display: flex;
  width: 75vw; /* Main container size */
  height: 75vh; /* Main container height */
  background-color: black;
  z-index: 9999;
  color: var(--primary-text-color);
  font-family: var(--primary-font-family);
  text-shadow: var(--primary-text-shadow);
}

/* Left Column (Overview and Forecast) */
.left-column {
  display: flex;
  flex-direction: column;
  width: 65%; /* 65% of the width */
  padding: 2rem;
  justify-content: space-between;
}

/* Right Column (Background Image and Details) */
.right-column {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  background-position: center;
  width: 35%; /* 35% of the width */
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  color: var(--primary-text-color);
  font-family: var(--primary-font-family);
  text-shadow: var(--primary-text-shadow);
}

/* Overview Section (Today’s Weather) */
.today-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  flex-grow: 1;
}

.today-header {
  font-style: italic;
  font-family: cursive;
  font-size: 1.5rem;
  color: var(--primary-text-color);
}

.today-temperature {
  font-size: 3rem;
  font-weight: bold;
}

/* Forecast Section */
.forecast-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  font-size: 1.2rem;
}

.forecast-title {
  font-weight: bold;
  font-size: 1.5rem;
}

.forecast-day {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
}

.forecast-day .weather-icon {
  font-size: 2rem;
}

.forecast-day .temperature-range {
  font-size: 1.2rem;
}

/* Small Details in Right Column */
.right-column .details {
  margin-top: auto;
  font-size: 1.2rem;
  color: var(--secondary-text-color);
}

.right-column .details p {
  margin-bottom: 1rem;
}

.left-column,
.right-column {
  width: 100%;
}
